.move-list-container{
    margin-top:40px;
    border-top:1px solid #303B4A;
    padding-top: 40px;
    display: flex;
    width: 100%;
    max-width: 1100px;
    justify-content: space-between;
    img{
        width: 50%;
        align-self: flex-start;
    }
    .moves{
        display: flex;
        align-items:flex-start ;
        flex-direction: column;
        h1{
            font-size:1.6rem;
        }
        h2,h3{
            font-size: 1.4rem;
            font-weight: 400;
        }
        h4{
            font-weight: 400;
            color: rgb(70, 70, 70);
            margin: 0;
        }
        .trans{
            margin-bottom:20px;
            border-bottom:1px solid rgba(48, 59, 74, 0.712);
            padding-bottom:10px;
            width: 100%;
        }
    }
}